<template>
  <div id="modal_update_profile" uk-modal>
    <div class="uk-modal-dialog uk-border-rounded uk-overflow-hidden">
      <button class="uk-modal-close-default" type="button" uk-close></button>
      <div class="uk-modal-header">
        <h2 class="uk-modal-title"> </h2>
      </div>
      <div v-if="$store.getters.isAuthenticated" class="uk-modal-body">
        <form @submit.prevent="update_profile">
          <!--          <fieldset class="uk-fieldset">-->
          <div class="uk-margin">
            <label class="uk-form-label" for="user_name"></label>
            <div class="uk-form-controls">
              <input v-model.trim="$v.name.$model" class="uk-input uk-border-rounded" :class="{ 'uk-form-danger': $v.name.$error, }" id="user_name" type="text" placeholder="">
              <div class="uk-text-danger" v-if="$v.name.$dirty && !$v.name.required"> </div>
              <div class="uk-text-danger" v-if="!$v.name.minLength">     {{$v.name.$params.minLength.min}} </div>
            </div>
          </div>

          <div class="uk-margin">
            <label class="uk-form-label" for="user_email">. </label>
            <div class="uk-form-controls">
              <input class="uk-input uk-border-rounded" v-model.trim="$v.email.$model" :class="{ 'uk-form-danger': $v.email.$error }" id="user_email" type="text" placeholder=".">
              <div class="uk-text-danger" v-if="$v.email.$dirty && !$v.email.required">.    </div>
              <div class="uk-text-danger" v-if="!$v.email.email">   </div>
            </div>
          </div>

          <div class="uk-margin">
            <label class="uk-form-label" for="old_password"> </label>
            <div class="uk-form-controls">
              <input class="uk-input uk-border-rounded" v-model.trim="$v.old_password.$model" :class="{ 'uk-form-warning': $v.old_password.$error }" id="old_password" type="text" placeholder=" ">
              <div class="uk-text-warning" v-if="!$v.old_password.required">  </div>
<!--              <div class="uk-text-danger" v-if="$v.old_password.$dirty && !$v.old_password.minLength">     6 </div>-->
            </div>
          </div>

          <div v-if="old_password || password || password_confirm" class="uk-margin">

            <label class="uk-form-label" for="password"> </label>
            <div class="uk-form-controls">
              <input class="uk-input uk-border-rounded" v-model.trim="$v.password.$model" :class="{ 'uk-form-warning': $v.password.$error }" id="password" type="text" placeholder=" ">
              <div class="uk-text-warning" v-if="!$v.password.required">    </div>
              <div class="uk-text-warning" v-if="!$v.password.minLength">     {{$v.password.$params.minLength.min}} </div>
            </div>
          </div>

          <div v-if="old_password && password || password_confirm" class="uk-margin">
            <label class="uk-form-label" for="password_confirm"> </label>
            <div class="uk-form-controls">
              <input class="uk-input uk-border-rounded" v-model.trim="$v.password_confirm.$model" :class="{ 'uk-form-warning': $v.password_confirm.$error }" id="password_confirm" type="text" placeholder="  ">
              <div class="uk-text-warning" v-if="!$v.password_confirm.required">    </div>
              <div class="uk-text-warning" v-if="$v.password_confirm.required && !$v.password_confirm.sameAsPassword">  </div>
            </div>
          </div>

          <!--          </fieldset>-->
        </form>
      </div>
      <div class="uk-modal-footer uk-text-right">
        <button class="uk-button uk-button-default uk-modal-close uk-margin-right" type="button">Cancel</button>
        <button class="uk-button uk-button-primary " @click.prevent="update_profile" type="button"> </button>
      </div>
    </div>
  </div>
</template>

<script>
import { required, requiredIf, sameAs, minLength, email } from 'vuelidate/lib/validators'
export default {
  name: "modal_update_profile",
  computed: {
    user() {
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.name = this.$store.getters.authUser.name
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.email = this.$store.getters.authUser.email
      return this.$store.getters.authUser
    }
  },
  data() {
    return {
      name: '',
      email: '',
      old_password: '',
      password: '',
      password_confirm: '',
    }
  },
  validations: {
    email: {
      required,
      email
    },
    name: {
      required,
      minLength: minLength(6),
    },
    old_password: {
      // !required -> , required  false --> 
      required: requiredIf(function () {
       if (this.password || this.password_confirm) {
         this.$v.old_password.$touch()
       } // this.old_password
        return this.password || this.password_confirm //   password   password_confirm
      })
    },
    password: {
      minLength: minLength(6),
      required: requiredIf(function () { // true --> 
        // if (!!this.old_password || !!this.password_confirm) { //      password password_confirm
        //   this.$v.old_password.$error = true
        //   this.$v.password_confirm.$error = true
        // }
        // if (this.old_password || this.password_confirm) {
        //
        // }
        if (this.old_password || this.password_confirm) {
          this.$v.password.$touch()
        }
        return this.old_password || this.password_confirm
      })
    },
    password_confirm: {
      sameAsPassword: sameAs('password'),
      required: requiredIf(function () {
        // if (!!this.password || !!this.old_password) { //      password password_confirm
        //   this.$v.password.setError(true);
        //   this.$v.old_password.setError(true);
        // }
        if (this.password || this.old_password) {
          this.$v.password_confirm.$touch()
        }
        return !!this.password || !!this.old_password
      })
    },
  },
  mounted() {
    this.name = this.$store.getters.authUser.name
    this.email = this.$store.getters.authUser.email
  },
  methods: {
    update_profile() {
      this.$store.dispatch('update_profile', this.$data).then(() => {
        // eslint-disable-next-line no-undef
        UIkit.notification('    ')
        // eslint-disable-next-line no-undef
        UIkit.modal(document.querySelector('#modal_update_profile')).hide();
      })
      .catch(() => {
        // eslint-disable-next-line no-undef
        UIkit.notification('      ')
      })
    }
  }
}
</script>

<style scoped>

</style>
